<template>
  <div class="main">
    <div class="gallery">
      <div class="image-cover" v-for="(image, index) in images" :key="index">
        <img :src="`img/gallery/${image.src}`" alt="" class="image" :class="{ loading: !image.isLoaded }" @click="openPopup(image.src)" @load="image.isLoaded = true">
        <div class="spinner" v-show="!image.isLoaded">
          <div class="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div>
        </div>
      </div>
    </div>
    <div class="popup" :class="{ active: isActive }" @click.self="closePopup">
      <img :src="`img/gallery/${popupImage}`" alt="" class="popup__image">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      popupImage: "",
      isActive: false,
      images: [
        {
          src: "gallery1.jpg",
          isLoaded: false
        },
        {
          src: "gallery2.jpg",
          isLoaded: false
        },
        {
          src: "gallery3.jpg",
          isLoaded: false
        },
        {
          src: "gallery4.jpg",
          isLoaded: false
        },
        {
          src: "gallery5.jpg",
          isLoaded: false
        },
        {
          src: "gallery6.jpg",
          isLoaded: false
        },
        {
          src: "gallery7.jpg",
          isLoaded: false
        },
        {
          src: "gallery8.jpg",
          isLoaded: false
        },
        {
          src: "gallery9.jpg",
          isLoaded: false
        },
        {
          src: "gallery10.jpg",
          isLoaded: false
        },
        {
          src: "gallery11.jpg",
          isLoaded: false
        },
      ]
    }
  },
  methods: {
    openPopup(image) {
      this.popupImage = image;
      this.isActive = true;
    },
    closePopup() {
      this.popupImage = "";
      this.isActive = false;
    }
  }
}
</script>

<style scoped>
  .main {
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px;
  }

  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: .5rem;
  }

  .image-cover {
    position: relative;
    overflow: hidden;
    max-height: 200px;
    background: rgba(0, 0, 0, .5);
  }

  .image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform .3s ease-out, opacity .3s ease-in-out;
    cursor: pointer;
  }

  .image:hover {
    transform: scale(1.2);
  }

  .image.loading {
    opacity: 0;
  }

  .popup {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .popup.active {
    opacity: 1;
    pointer-events: all;
  }

  .popup__image {
    position: fixed;
    width: 60%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    box-shadow: 0 0 15px rgb(0, 0, 0);
  }

  @media (max-width: 1400px) {
    .popup__image {
      width: 70%;
    }
  }

  @media (max-width: 1000px) {
    .popup__image {
      width: 95%;
    }
  }

  .spinner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .lds-roller {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-roller div {
    animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background: #fff;
    margin: -4px 0 0 -4px;
  }
  .lds-roller div:nth-child(1) {
    animation-delay: -0.036s;
  }
  .lds-roller div:nth-child(1):after {
    top: 63px;
    left: 63px;
  }
  .lds-roller div:nth-child(2) {
    animation-delay: -0.072s;
  }
  .lds-roller div:nth-child(2):after {
    top: 68px;
    left: 56px;
  }
  .lds-roller div:nth-child(3) {
    animation-delay: -0.108s;
  }
  .lds-roller div:nth-child(3):after {
    top: 71px;
    left: 48px;
  }
  .lds-roller div:nth-child(4) {
    animation-delay: -0.144s;
  }
  .lds-roller div:nth-child(4):after {
    top: 72px;
    left: 40px;
  }
  .lds-roller div:nth-child(5) {
    animation-delay: -0.18s;
  }
  .lds-roller div:nth-child(5):after {
    top: 71px;
    left: 32px;
  }
  .lds-roller div:nth-child(6) {
    animation-delay: -0.216s;
  }
  .lds-roller div:nth-child(6):after {
    top: 68px;
    left: 24px;
  }
  .lds-roller div:nth-child(7) {
    animation-delay: -0.252s;
  }
  .lds-roller div:nth-child(7):after {
    top: 63px;
    left: 17px;
  }
  .lds-roller div:nth-child(8) {
    animation-delay: -0.288s;
  }
  .lds-roller div:nth-child(8):after {
    top: 56px;
    left: 12px;
  }
  @keyframes lds-roller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
</style>