import Vue from 'vue';
import VueRouter from 'vue-router';
import Main from '../components/Main.vue';
import Band from '../components/Band.vue';
import Liam from '../components/Liam.vue';
import Noel from '../components/Noel.vue';
import Bonehead from '../components/Bonehead.vue';
import Guigsy from '../components/Guigsy.vue';
import Tony from '../components/Tony.vue';
import Alan from '../components/Alan.vue';
import Gem from '../components/Gem.vue';
import Andy from '../components/Andy.vue';
import Music from '../components/Music.vue';
import Photos from '../components/Photos.vue';

Vue.use(VueRouter);

const routes = [
  { path: '/', component: Main },
  { path: '/band', component: Band },
  { path: '/liam', component: Liam },
  { path: '/noel', component: Noel },
  { path: '/bonehead', component: Bonehead },
  { path: '/guigsy', component: Guigsy },
  { path: '/tony', component: Tony },
  { path: '/alan', component: Alan },
  { path: '/gem', component: Gem },
  { path: '/andy', component: Andy },
  { path: '/music', component: Music },
  { path: '/photos', component: Photos }
];

export default new VueRouter({
    mode: 'history',
    routes
});